body {
  background: #fff;
}
.header {
  display: flex;
  justify-content: space-between;
  padding: 0 24px;
  background: #fff;
  z-index: 999;
}
.ant-menu-horizontal {
  border-bottom: none;
  color: #999;
}
.ant-menu-horizontal > .ant-menu-item {
  -webkit-transition: 0ms;
  transition: 0ms;
}
.ant-menu-horizontal > .ant-menu-item:hover {
  color: #f16529;
  border-bottom: none;
}
.ant-menu-item-selected,
.ant-menu-item-active {
  color: #333 !important;
  font-weight: bold;
}
.ant-menu-horizontal > .ant-menu-item-selected:hover {
  color: #f16529;
  border-bottom: 2px solid #f16529;
}
.ant-dropdown-link {
  color: #333;
}
.ant-dropdown-menu-item {
  width: 160px;
  padding: 8px 16px;
}
.ant-form label {
  font-size: 12px;
  color: #AAA;
}
.ant-form-vertical .ant-form-item {
  padding-bottom: 0;
  margin-bottom: 16px;
}
.ant-layout-content {
  background: #FFFFFF;
  box-shadow: 0 2px 20px 0 rgba(0, 0, 0, 0.1);
}
.cpLogo {
  margin: 0;
  color: #f16529;
  font-weight: bolder;
  margin-right: 8px;
}
